.workPage{
    width: 100%;
    padding: 0 50px 100px 50px;
    h1{
        font-size: 2.75em;
        margin-bottom: 10px;
    }
    div.notimp{
        opacity: 0.5;
    }

    .workGrid{
        width: 100%;
        // height: fit-content;
        margin-top: 50px;
        display: flex;
        flex-wrap: wrap;
        // justify-content: space-evenly;
        gap: 40px;
        .workBox{
            width: calc(50% - 40px);
            aspect-ratio: 5/3;
            min-width: 400px;
            min-height: 240px;
            .workCard{
                width: 100%;
                height: 100%;
                background-color: #fff;
                background-color: var(--mid-bg-color);
                border-radius: 20px;
                box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.15);
                padding: 20px;
                display: flex;
                flex-direction: column;
                gap: 10px;
                cursor: pointer;
                transition: .25s ease;
                &:hover{
                    transform: scale(1.01);
                }
                span{
                    font-size: 1.5em;
                    font-weight: bold;
                }
                .workPreview{
                    width: 100%;
                    flex: 1;
                    background-color: #000;
                    border-radius: 20px;
                    background-size: cover;
                    background-repeat: no-repeat;
                    background-position: center;
                }

                .workPreview[data-id="fullstack"]{
                    background-image: url(../../img/fullstackprvw.png);
                }
                .workPreview[data-id="uiux"]{
                    background-image: url(../../img/uiuxprvw.png);
                }
                .workPreview[data-id="webtools"]{
                    background-image: url(../../img/webtoolsprvw.png);
                }
                .workPreview[data-id="freelance"]{
                    background-image: url(../../img/freelanceprvw.png);
                }
                .workPreview[data-id="graphicdesign"]{
                    background-image: url(../../img/graphicprvw.png);
                }
            }
        }
    }
}

@media screen and (max-width: 820px) {
    .workGrid{
        justify-content: center;
    }
    .workPage{
        padding: 0 50px 100px 50px;
        .workBox{
            min-width: 290px !important;
        }
    }
}